<template>
  <v-container fluid grid-list-sm>
    <!-- snackbar -->
    <v-snackbar v-model="snackbar" color="failure" :timeout="5000" top>
      <span>{{error}}</span>
      <v-btn flat color="white" @click="snackbar=false">
        <v-icon>close</v-icon>
      </v-btn>
    </v-snackbar>
    <span v-if="feedback">{{feedback}}</span>
    <span v-if="photos">{{photos.length}} photos from Mars matched your search criteria</span>
    <v-container fluid grid-list-sm>
      <v-layout row wrap>
        <v-flex v-for="photo in photos" :key="photo.id" xs4>
          <img :src="getImgUrl(photo.img_src)" class="image" alt="lorem" width="100%" height="100%">
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
import Photo from "@/components/PhotoComponent";

export default {
  data() {
    return {
      snackbar: false,
      timeout: 10000,
      totalPhotos: null,
      feedback: ""
    };
  },
  methods: {
    //v-img wont show if the url is directly bound in the template
    getImgUrl: function(img) {
      return img;
    }
  },
  computed: {
    photos() {
      if (this.$store.getters.getPhotos.length) {
        this.feedback = "";
        return this.$store.getters.getPhotos;
      } else {
        this.feedback = "No photos matching your search criteria was found:(";
      }
    },
    error() {
      if (this.$store.getters.getError) {
        this.snackbar = true;
        return this.$store.getters.getError;
      }
    }
  }
};
</script>

<style>
.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>
