<template>
  <v-layout column align-center class="my-container">
    <v-flex>
      <v-form ref="form">
        <v-text-field
          @click:append="$refs.form.reset()"
          type="text"
          placeholder="Enter a number"
          append-icon="close"
          v-model.trim="input"
          :rules="soloRules"
          name="sol"
        ></v-text-field>
        <v-overflow-btn
          :items="cameras"
          v-model="camera"
          label="Choose camera"
          :rules="cameraRules"
          name="camera"
        ></v-overflow-btn>
        <v-btn @click.prevent="search">
          Search
          <v-icon right>search</v-icon>
        </v-btn>
      </v-form>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data: function() {
    return {
      input: "",
      camera: "",
      photos: [],
      cameras: [
        "FHAZ",
        "NAVCAM",
        "MAST",
        "CHEMCAM",
        "MAHLI",
        "MARDI",
        "RHAZ",
        "ANY"
      ],
      soloRules: [
        v => !!v || "Input is required, please input a number ",
        v => /^[0-9 ]*$/.test(v) || "Only digits allowed!"
      ],
      cameraRules: [v => !!v || "You must choose a camera"]
    };
  },

  methods: {
    search: function() {
      if (this.$refs.form.validate()) {
        this.$store.dispatch("getPhotos", {
          sol: this.input,
          camera: this.camera
        });
      } else {
        console.log("validation failed");
        this.$store.dispatch("error", "Validation failed");
      }
    }
  }
};
</script>


<style>
.my-container {
}
.gallery {
}
</style>
