import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    photos: [],
    error: ""
  },
  //best practice not to access the state directly, but through getters
  getters: {
    getError: state => {
      return state.error;
    },
    getPhotos: state => {
      return state.photos;
    }
  },
  //no async code!Only way to change state
  mutations: {
    getError(state, errormessage) {
      state.error = errormessage;
    },
    getPhotos(state, data) {
      state.photos = data.photos;
    }
  },
  actions: {
    error(context, errormessage) {
      context.commit("getError", errormessage);
    },
    getPhotos(context, query) {
      const request = axios.post("/api/photos", {
        sol: query.sol,
        camera: query.camera
      });
      return request
        .then(response => {
          context.commit("getPhotos", {
            photos: response.data.photos
          });
          return response.data.photos;
        })
        .then(response =>
          response.data.photos.length
            ? ""
            : this.$store.dispatch(
                "error",
                "No photos matched your search criteria"
              )
        )
        .catch(error => {
          context.commit("getError", error.message);
        });
    }
  }
});
